.project {
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.project-text {
    width: 65%;
}

.project-title {
    font-size: 50px;
    font-weight: 600;
}

.project-desc {
    margin: 20px 0px;
}

.project-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}