body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;

}
body{
  background-color: bisque;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main {
    display: flex;
    height: 50vh;

}

.main-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.main-right {
    flex: 1 1;
}

.main-left-wrap {
    padding: 5px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.introduction {
    font-size: 30px;
    font-weight: 300;
}

.name {
    font-size: 60px;
}

.title {
    height: 50px;
}

.title-wrap {
    height: 100%;
}

.title-1 {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: blue;
    display: flex;
    align-items: center;
}

.img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.pr {
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 15px 15px 15px 15px;
    overflow: hidden;
}

.project-img {
    width:100%;
    object-fit:cover;
}


.project {
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.project-text {
    width: 65%;
}

.project-title {
    font-size: 50px;
    font-weight: 600;
}

.project-desc {
    margin: 20px 0px;
}

.project-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}
.footer {
    position: fixed;
    width: 100%;
    bottom: 10px;
    font-size: 25px;
   }

.footer-i {
    color: black;
    display: flex;
    font-size: 15px;
    align-items: center;
    justify-content: center;
    padding-right: 50px
}
